.postid-#{$kabarai_id} {
	h1 {
		&.valign {
			text-align: left !important;
		}
	}
	.sub-nav .active {
		@extend .color-kabarai;
	}
	// 
	.column-1-3 {
		@include breakpoint( small only ) {
			height: 220px;
		}
	}
	.slide-item {
		@extend .text-center;
		z-index: 1;
		margin: 0 auto;
		width: 210px;
		height: 210px;
		border-radius: 210px;
		img {
			width: 210px;
			height: 210px;
			border-radius: 210px;
		}
	}
	#js-clock {
		background-color: $white;
		width: 210px;
		height: 210px;
		border-radius: 210px;
		border: 5px solid $white;
		@extend .m0-auto;
		@extend .b-shadow-1;
	}
	[class$="-hand"] {
		position: absolute;
		transition: all .8s ease-out;
		background-color: $white;
		width: 12px;
		margin-left: -6px;
		left: 50%;
		bottom: 105px;
		border-radius: 12px;
		z-index: 10000;
		@extend .b-shadow-1;
	}
	.minute-hand {
		height: 90px;
		transform-origin: 50% 85px;
	}
	.hour-hand {
		height: 75px;
		transform-origin: 50% 70px;
	}
	#section-1 {
		.grid-item {
			@include clearfix();
			margin: 0 auto;
			p { text-align: left !important; }
			&:nth-child(2n) {
				margin-bottom: 2rem;
				border-bottom: 1px dashed $gainsboro;
				@include breakpoint( large ) {
					border-bottom: 0;
				}
			}
			@include breakpoint( large ) {
				border-bottom: 0;
				margin-bottom: 4rem;
			}
		}
		.fa-stack {
			float: left;
			clear: left;
			top: 0.5rem;
			margin-right: 0.5rem;
		}
	}
}