#grid-members {
	margin: 3rem auto;
	.grid-item {
		@extend .text-center;
		@extend .p0;
		&:hover {
			.img-wrapper {
				overflow: hidden;
				img {
					transform: scale(.9);
				}
			}
			h2 {
				color: $primary-color;
				span {
					color: $primary-color !important;
				}
			}
		}
		.img-wrapper {
			margin-bottom: 10px;
			width: 210px;
			height: 210px;
			img {
				@include hacks;
				transition: all $slowDuration;
				width: 210px;
				height: 210px;
				border-radius: 210px;
			}
		}
		h2 {
			transition: color $mediumDuration;
			background-color: transparent;
			border-radius: none;
			padding: 0;
			margin: .2rem auto .5rem;
			.subtitle {
				transition: color $mediumDuration;
				display: block;
				margin: 10px;
				font-size: 14px;
			}
		}
	}
}