.waves-effect {
	position: relative;
	cursor: pointer;
	display: inline-block;
	overflow: hidden;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	// white-space: nowrap;
	// outline: 0;

	vertical-align: middle;
	// cursor: pointer;
	// border: none;
	// outline: none;
	// color: inherit;
	// background-color: rgba(0, 0, 0, 0);
	// font-size: 1em;
	// line-height:1em;
	// text-align: center;
	// text-decoration: none;
	z-index: 1;
	will-change: opacity, transform;
	transition: all .3s ease-out;

	.waves-ripple {
		position: absolute;
		border-radius: 50%;
		width: 20px;
		height: 20px;
		margin-top:-10px;
		margin-left:-10px;
		opacity: 0;

		background: rgba(0,0,0,0.2);
		// $gradient: rgba(0,0,0,0.2) 0,rgba(0,0,0,.3) 40%,rgba(0,0,0,.4) 50%,rgba(0,0,0,.5) 60%,rgba(255,255,255,0) 70%;
		// background: -webkit-radial-gradient($gradient);
		// background: -o-radial-gradient($gradient);
		// background: -moz-radial-gradient($gradient);
		// background: radial-gradient($gradient);
		transition: all 0.7s ease-out;
		-webkit-transition-property: -webkit-transform, opacity;
		-moz-transition-property: -moz-transform, opacity;
		-o-transition-property: -o-transform, opacity;
		transition-property: transform, opacity;
		transform: scale(0);
		pointer-events: none;
	}

	// Waves Colors
	&.waves-light .waves-ripple {
	  background-color: rgba(255, 255, 255, 0.45);
	}

	&.waves-red .waves-ripple {
	  background-color: rgba(244, 67, 54, .70);
	}
	&.waves-yellow .waves-ripple {
	  background-color: rgba(255, 235, 59, .70);
	}
	&.waves-orange .waves-ripple {
	  background-color: rgba(255, 152, 0, .70);
	}
	&.waves-purple .waves-ripple {
	  background-color: rgba(156, 39, 176, 0.70);
	}
	&.waves-green .waves-ripple {
	  background-color: rgba(76, 175, 80, 0.70);
	}
	&.waves-teal .waves-ripple {
	  background-color: rgba(0, 150, 136, 0.70);
	}

	// Style input button bug.
	input[type="button"], input[type="reset"], input[type="submit"] {
		border: 0;
		font-style: normal;
		font-size: inherit;
		text-transform: inherit;
		background: none;
	}

}

.waves-notransition {
	transition: none !important;
}

.waves-circle {
	transform: translateZ(0);
	-webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
}

